import Navbar from "../ARCASideNav/Navbar";
import "./NetworkStorage.css";
import { useState, useEffect } from "react";
import NasIcon from "../../assets/Icons/NAS_or.svg";
import { TextField, Grid, Button, Typography, Switch } from "@material-ui/core";
import axios from "axios";
import LinearProgress from "@material-ui/core/LinearProgress";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import LogoutPopup from "../../LogoutPopup";

const NetworkStorage = () => {
  const [networkStorageForm, setNetworkStorageForm] = useState({
    userName: "",
    password: "",
  });

  const handleUserName = (event) => {
    setNetworkStorageForm({
      ...networkStorageForm,
      userName: event.target.value,
    });
  };
  const handlePassword = (event) => {
    setNetworkStorageForm({
      ...networkStorageForm,
      password: event.target.value,
    });
  };

  const submitNetworkStorageForm = (e) => {
    e.preventDefault();
    console.log(networkStorageForm);
    axios
      .post("/addnas", networkStorageForm)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const [nsToggle, setNsToggle] = useState(true);

  const toggler = (event) => {
    setNsToggle(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/nason").catch((err) => console.log(err));
    } else if (event.target.checked === false) {
      const resp = axios.get("/nasoff").catch((err) => console.log(err));
    }
  };

  useEffect(() => {
    async function getStatus() {
      try {
        const res = await axios.get("/nasstatus");
        setNsToggle(res.data.result);
      } catch (error) {
        console.log(error);
      }
    }
    getStatus();
  }, []);



  const BorderLinearProgress = withStyles((theme) => ({
    root: {
      height: 10,
      borderRadius: 5,
    },
    colorPrimary: {
      backgroundColor:
        theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
    },
    bar: {
      borderRadius: 5,
      backgroundColor: "#EF8029",
    },
  }))(LinearProgress);

  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="ns-container">
        <Grid container direction="row" spacing={10}>
          <Grid item>
            <img src={NasIcon} className="network-logo" />
            <h5 className="ns-titles">Network Attached Storage</h5>
            <h6 className="ns-sub">Information about Network Storage</h6>
          </Grid>
          <Grid xs={4}>
            <div style={{ marginTop: "51px", marginLeft: "140px" }}>
              <Typography component="div">
              <Grid item style={{marginLeft: -20, marginBottom: -32}} >Off</Grid>
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>
                    <Switch
                      checked={nsToggle}
                      onChange={toggler}
                      color="primary"
                      name="ns-toggle"
                    ></Switch>
                  </Grid>
                </Grid>
                <Grid item style={{marginLeft: 60, marginTop: -32}}>On</Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: "15px", marginRight: "15px", marginTop: "-55px", color:"#E0E5E5" }} />
        {/* <Typography style={{ marginLeft: "22px" }}>Device Details</Typography>
        <Typography style={nsToggle ? {color:"green", marginLeft:"22px"}:{color:'#e0e0e0', marginLeft:"22px"}} gutterBottom>Status: Connected</Typography> */}
        <Grid
          container
          direction="row"
          spacing={2}
          style={{ marginLeft: "15px", marginTop: "10px" }}
        >
          <Grid item>
            <form onSubmit={submitNetworkStorageForm}>
              <Grid container direction="column" spacing={2}>
                <Grid item>
                  <TextField
                    label="Username"
                    id="outlined-size-small"
                    variant="outlined"
                    size="small"
                    value={networkStorageForm.userName}
                    onChange={handleUserName}
                    disabled={!nsToggle}
                    disabled
                  />
                </Grid>
                <Grid item>
                  <TextField
                    label="Password"
                    id="outlined-size-small"
                    variant="outlined"
                    type="password"
                    size="small"
                    value={networkStorageForm.password}
                    onChange={handlePassword}
                    disabled={!nsToggle}
                    disabled
                  />
                </Grid>
                <Grid item>
                  <Button
                    variant="contained"
                    type="submit"
                    className="sbmt"
                    color="primary"
                    disabled={!nsToggle}
                    disabled
                  >
                    <span style={{ color: "white", fontWeight: "bolder",width: 85 }}>
                      Save
                    </span>
                  </Button>
                </Grid>
              </Grid>
            </form>
          </Grid>
          {/* <div className="storage-info" style={!nsToggle ?{color:'#e0e0e0'}:{}}>
            <Typography>Device Storage:</Typography>
          </div> */}
        </Grid>
        {/* <div className="progress-bar" >
          <BorderLinearProgress variant="determinate" value={50} />
        </div> */}
      </div>
    </>
  );
};

export default NetworkStorage;
