import { useEffect, useState } from "react";
import axios from "axios";
import parentalIcon from "../../assets/icons_v2/nav_parental_or.svg";
import "./ParentalControl.css";
import {
  Grid,
  Button,
  Typography,
  TextareaAutosize,
  FormControlLabel,
  CircularProgress,
  Switch,
  ButtonGroup,
  TextField,
  form
} from "@material-ui/core";
import Navbar from "../ARCASideNav/Navbar";
import LogoutPopup from "../../LogoutPopup";

const ParentalControl = () => {
  const [whiteListTextArea, setWhiteListTextArea] = useState("");
  const [blackListTextArea, setBlackListTextArea] = useState("");
  const [blackListValue, setBlackListValue] = useState("");
  const [whiteListValue, setWhiteListValue] = useState("");
  const [uLevelToggle, setULevelToggle] = useState(false);
  const [uaLevelToggle, setUALevelToggle] = useState(false);
  const [aLevelToggle, setALevelToggle] = useState(false);
  const [dnsData, setDnsData] = useState(null);
  const [errors, setErrors] = useState({});
  const [pctoggle, setPctoggle] = useState({
    checkedA: true,
  });
  const handleChangeToggle = (event) => {
    setPctoggle({ ...pctoggle, [event.target.name]: event.target.checked });
    if (event.target.checked === true) {
      const res = axios.get("/parentalon");
      console.log(res);
    } else if (event.target.checked === false) {
      const resp = axios.get("/parentaloff");
      console.log(resp);
    }
  };

  async function getData() {
    const parentstatus = await axios
      .get("/parentstatus")
      .catch((err) => console.log(err));
    setPctoggle({ checkedA: parentstatus.data.result });
    const resp = await axios
      .get("/editblacklist")
      .catch((err) => console.log(err));
    setBlackListTextArea(resp.data.result);
    const response = await axios
      .get("/editwhitelist")
      .catch((err) => console.log(err));
    setWhiteListTextArea(response.data.result);
    const ulevelstatus = await axios
      .get("/ulevelstatus")
      .catch((err) => console.log(err));
    setULevelToggle(ulevelstatus.data.result);
    const ualevelstatus = await axios
      .get("/ualevelstatus")
      .catch((err) => console.log(err));
    setUALevelToggle(ualevelstatus.data.result);
    const alevelstatus = await axios
      .get("/alevelstatus")
      .catch((err) => console.log(err));
    setALevelToggle(alevelstatus.data.result);
    const res = await axios.get("/dnsreport");
    setDnsData(res.data);
  }

  useEffect(() => {
    getData();
    const interval = setInterval(()=>{
      getData();
    },10000)
    return ()=>clearInterval(interval);
  }, []);

  const handleBlackListValue = (e) => {
    setBlackListValue(e.target.value);
  };

  const handleAddBlackList = () => {
    setBlackListValue("")
    axios.post("/saveblacklist", blackListValue);
    console.log(blackListValue);
    getData();
  };
  const handleWhiteListValue = (e) => {
    setWhiteListValue(e.target.value);
  };

  const handleAddWhiteList = () => {
    setWhiteListValue("")
    axios.post("/savewhitelist", whiteListValue);
    console.log(whiteListValue);
    getData();
  };

  const handleDeleteBlackList = () => {
    setBlackListValue("")
    axios.post('/deleteblacklist', blackListValue);
    getData();
  };
  const handleDeleteWhiteList = () => {
    setWhiteListValue("")
    axios.post('/deletewhitelist', whiteListValue);
    getData();
  };

  const handleChangeUToggle = (event) => {
    setULevelToggle(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/ulevelon");
      console.log(res);
    }
    else if (event.target.checked === false) {
      const res = axios.get("/uleveloff");
      console.log(res);
    }
  };

  const handleChangeUaToggle = (event) => {
    setUALevelToggle(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/ualevelon");
      console.log(res);
    }
    else if (event.target.checked === false) {
      const res = axios.get("/ualeveloff");
      console.log(res);
    }
  };

  const handleChangeAToggle = (event) => {
    setALevelToggle(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/alevelon");
      console.log(res);
    }
    else if (event.target.checked === false) {
      const res = axios.get("/aleveloff");
      console.log(res);
    }
  };

  const [showDNS, setShowDNS] = useState(true);
  const [showWhiteList, setShowWhiteList] = useState(false);
  const [showBlackList, setShowBlackList] = useState(false);
  const handleDNS = () => {
    setShowDNS(true);
    setShowWhiteList(false);
    setShowBlackList(false);
  };
  const handleBlackList = () => {
    setShowBlackList(true);
    setShowDNS(false);
    setShowWhiteList(false);
    const res = axios.get("/editblacklist");
    console.log(res);
  };
  const handleWhiteList = () => {
    setShowWhiteList(true);
    setShowDNS(false);
    setShowBlackList(false);
    const res = axios.get("/editwhitelist");
    console.log(res);
  };

  let showDiv;
  if (showDNS === true && showWhiteList === false && showBlackList === false) {
    showDiv = (
      <div className="dns-container">
        <div className="dns-content">
          <Typography style={!pctoggle.checkedA ? { color: '#dddddd', margin: "revert" } : { margin: "revert" }}>
            This tab shows the last generated DNS Report
          </Typography>
        </div>
        <hr style={{ marginRight: 15, color: "#E0E5E5" }} />
        <h3 variant="h5" className="dns-request" style={!pctoggle.checkedA ? { color: '#dddddd' } : {}}>
          Latest DNS Requests
        </h3>
        
        {dnsData ? (
          <div style={{margin: "-10px 15px 20px", marginLeft:"1px"}}>
          <div className="table-body">
          <table style={{fontFamily:"MONTSERRAT-LIGHT" }}>
          <col width="100px" />
          <col width="80px" />
          <col width="100px" />
            <thead>
              <tr style={!pctoggle.checkedA ? { color: '#F6F6F6' } : {}}>
                <th style={{ textAlign: "inherit" }}>Date</th>
                <th style={{ textAlign: "inherit" }}>Time</th>
                <th style={{ textAlign: "inherit" }}>Client</th>
                <th style={{ textAlign: "center" }}>Domain</th>
              </tr>
            </thead>
            
            <tbody >
              {dnsData.map((val, index) => {
                return (
                  <tr key={index} style={!pctoggle.checkedA ? { color: '#dddddd' } : {}}>
                    <td>{val[0]}</td>
                    <td>{val[1]}</td>
                    <td>{val[2]}</td>
                    <td style={{padding:7, cursor:"pointer"}} title={val[3]}>{val[3]}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          </div>
          </div> 
        )
        
        : (
          <div className="parental-loader">
            <CircularProgress />
          </div>
        )}
      </div>
    );
  } else if (
    showDNS === false &&
    showWhiteList === false &&
    showBlackList === true
  ) {
    showDiv = (
      <div className="blacklist-container">
        <Typography style={!pctoggle.checkedA ? { color: '#dddddd', margin: "revert" } : { margin: "revert" }}>
          This tab shows the local adblock blacklist
        </Typography>

        <Grid style={{ marginTop: "10px", marginBottom: "10px" }}>
        <form>
          <TextField
            size="small"
            type="text"
            variant="outlined"
            value={blackListValue}
            onChange={handleBlackListValue}
            disabled={!pctoggle.checkedA}
            autoComplete="off" 
            onInput={(e) => { e.target.value = e.target.value.replace(/[^0-9\wd\-_.,+@!#$%&*()?/"':;]/g, '') }}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleAddBlackList}
            style={{ marginLeft: "10px", marginTop: "2px" }}
            disabled={!pctoggle.checkedA}
          >
            <span style={{ color: "white" }}>Add</span>
          </Button>
          <Button
            variant="contained"
            color="primary"
            disabled={!pctoggle.checkedA}
            onClick={handleDeleteBlackList}
            style={{ marginLeft: "10px", marginTop: "2px" }}
          >
            <span style={{ color: "white" }}>Delete</span>
          </Button>
          </form>
        </Grid>
        {blackListTextArea ?
        <div style={{margin: "0px 20px 20px 1px"}}>
          <table style={{fontFamily:"MONTSERRAT-LIGHT"}}>
            <thead>
              <tr style={!pctoggle.checkedA ? { color: '#f3efef' } : {}}>
                <th>Blacklisted</th>
              </tr>
            </thead>
            <tbody>
              {blackListTextArea.map((el) => {
                return (
                  <tr>
                    <td style={!pctoggle.checkedA ? { color: '#dddddd' } : {}}>{el}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          </div> : 
          <div className="blacklist-loader">
          <CircularProgress />
          </div>}
      </div>
    );
  } else if (
    showDNS === false &&
    showWhiteList === true &&
    showBlackList === false
  ) {
    showDiv = (
      <div className="whitelist-container">
        <Typography style={!pctoggle.checkedA ? { color: '#dddddd', margin: "revert" } : { margin: "revert" }}>
          This tab shows the local adblock whitelist
        </Typography>

        <Grid style={{ marginTop: "10px", marginBottom: "10px" }}>
          <TextField
            size="small"
            variant="outlined"
            value={whiteListValue}
            onChange={handleWhiteListValue}
            disabled={!pctoggle.checkedA}
            onInput={(e) => { e.target.value = e.target.value.replace(/[^0-9\wd\-_.,+@!#$%&*()?/"':;]/g, '') }}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleAddWhiteList}
            className="parental-add"
            style={{ marginLeft: "10px", marginTop: "2px" }}
            disabled={!pctoggle.checkedA}
          >
            <span style={{ color: "white" }}>Add</span>
          </Button>
          <Button
            variant="contained"
            color="primary"
            disabled={!pctoggle.checkedA}
            onClick={handleDeleteWhiteList}
            style={{ marginLeft: "10px", marginTop: "2px" }}
          >
            <span style={{ color: "white" }}>Delete</span>
          </Button>
        </Grid>
        {whiteListTextArea ?
          <div style={{margin: "0px 20px 20px 1px"}}>
          <table style={{fontFamily:"MONTSERRAT-LIGHT"}}>
            <thead>
              <tr style={!pctoggle.checkedA ? { color: '#f3efef' } : {}}>
                <th>Whitelisted</th>
              </tr>
            </thead>
            <tbody>
              {whiteListTextArea.map((el) => {
                return (
                  <tr>
                    <td style={!pctoggle.checkedA ? { color: '#dddddd' } : {}}>{el}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          </div> : 
          <div className="whitelist-loader">
          <CircularProgress />
          </div>}
      </div>
    );
  }
  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="p-container">
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <img
              src={parentalIcon}
              alt="Parental Control Icon"
              className="parental-icon"
            />
            <h1 className="name">Parental Control</h1>
            <h2 className="sub-name">Information about Parental Control</h2>
          </Grid>
          <Grid xs={4}>
            <div style={{ marginTop: "30px", marginLeft: "140px" }}>
              <Typography component="div">
              <Grid item style={{marginLeft: -20, marginBottom: -32}} >Off</Grid>
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                 
                  <Grid item>
                    <Switch
                      checked={pctoggle.checkedA}
                      onChange={handleChangeToggle}
                      color="primary"
                      name="checkedA"
                      inputProps={{ "aria-label": "primary checkbox" }}
                    />
                  </Grid>
                  
                </Grid>
                <Grid item style={{marginLeft: 60, marginTop: -32}}>On</Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: 15, marginRight: 15, color: "#E0E5E5" }} />
        <Grid container spacing={3} direction="row" >
          <Grid item style={{marginLeft: -10}}>
            <FormControlLabel
              className="parental-level"
              control={
                <Switch
                  checked={uLevelToggle}
                  onChange={handleChangeUToggle}
                  color="primary"
                  name="ulevel"
                  inputProps={{ "aria-label": "primary checkbox" }}
                  disabled={!pctoggle.checkedA}
                />
              }
              label="U Level"
            />
          </Grid>
          <Grid item>
            <FormControlLabel
              className="parental-level"
              control={
                <Switch
                  checked={uaLevelToggle}
                  onChange={handleChangeUaToggle}
                  color="primary"
                  name="ualevel"
                  inputProps={{ "aria-label": "primary checkbox" }}
                  disabled={!pctoggle.checkedA}
                />
              }
              label="U/A Level"
            />
          </Grid>
          <Grid item>
            <FormControlLabel
              className="parental-level"
              control={
                <Switch
                  checked={aLevelToggle}
                  onChange={handleChangeAToggle}
                  color="primary"
                  name="alevel"
                  inputProps={{ "aria-label": "primary checkbox" }}
                  disabled={!pctoggle.checkedA}
                />
              }
              label="A Level"
            />
          </Grid>
        </Grid>
        {pctoggle.checkedA ?
          (<div className="new-container">
            <ButtonGroup size="small" aria-label="small outlined button group">
              <Button
                onClick={handleDNS}
                style={showDNS ? { backgroundColor: "#f3d4bb" } : {}}
                disabled={!pctoggle.checkedA}
              >
                DNS Report
              </Button>
              <Button
                onClick={handleBlackList}
                style={showBlackList ? { backgroundColor: "#f3d4bb" } : {}}
                disabled={!pctoggle.checkedA}
              >
                Edit Blacklist
              </Button>
              <Button
                onClick={handleWhiteList}
                style={showWhiteList ? { backgroundColor: "#f3d4bb" } : {}}
                disabled={!pctoggle.checkedA}
              >
                Edit Whitelist
              </Button>
            </ButtonGroup>
            {showDiv}
          </div>) :
          (
            <div className="new-container">
              <ButtonGroup size="small" aria-label="small outlined button group">
                <Button
                  style={{ backgroundColor: "#dddddd" }}
                  disabled={!pctoggle.checkedA}
                >
                  DNS Report
                </Button>
                <Button
                  style={{ backgroundColor: "#dddddd" }}
                  disabled={!pctoggle.checkedA}
                >
                  Edit Blacklist
                </Button>
                <Button
                  style={{ backgroundColor: "#dddddd" }}
                  disabled={!pctoggle.checkedA}
                >
                  Edit Whitelist
                </Button>
              </ButtonGroup>
              {showDiv}
            </div>
          )}
      </div>
    </>
  );
};

export default ParentalControl;
