import { React, useState, useEffect } from "react";
import axios from "axios";
import Modal from "@material-ui/core/Modal";
import MenuItem from "@material-ui/core/MenuItem";
import CloseIcon from "@material-ui/icons/Close";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  Grid,
  CardActions,
  CardContent,
  Button,
  Typography,
  CircularProgress,
  TextField,
} from "@material-ui/core";
import green_dot from "../../assets/Icons/green_dot.svg";
import red_dot from "../../assets/Icons/red_dot.svg";
import wan from "../../assets/icons_v2/WAN.svg";
import wifiIcon from "../../assets/icons_v2/enable_wifi.svg";
import Navbar from "../ARCASideNav/Navbar";
