import Navbar from "../ARCASideNav/Navbar";
import axios from "axios";
import lan from "../../assets/icons_v2/LAN_or.svg";
import "./LAN.css";
import {
  Grid,
  Button,
  Typography,
  TextField,
  MenuItem,
  CircularProgress,
  Snackbar,
} from "@material-ui/core";
import React from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { useEffect, useState } from "react";
import LogoutPopup from "../../LogoutPopup";

const LAN = () => {
  const [tableData, setTableData] = useState(null);
  const [lanForm, setLanForm] = useState({
    dhcp:""
  });
  const [errors, setErrors] = useState({});
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
const [saveDisable,setSaveDisable] = useState(false);
const [saveDisableCheck,setSaveDisableCheck] = useState({})

  async function getLan() {
    try{
      const res = await axios.get("/getlan");
      setLanForm({
        ...lanForm,
        mac: res.data.result.macAddress,
        ip: res.data.result.ipAddress,
        subnet: res.data.result.subnetAddress,
        gateway: res.data.result.gateway,
        dhcp: res.data.result.dhcp,
        ippool1: res.data.result.ipPool1,
        ippool2: res.data.result.ipPool2,
      });
      try{
        const res = await axios.get("http://192.168.1.254:5000/getlan");
        setLanForm({
          ...lanForm,
          mac: res.data.result.macAddress,
          ip: res.data.result.ipAddress,
          subnet: res.data.result.subnetAddress,
          gateway: res.data.result.gateway,
          dhcp: res.data.result.dhcp,
          ippool1: res.data.result.ipPool1,
          ippool2: res.data.result.ipPool2,
        });
      }
      catch (error) {
        console.error(error);
      }
    }catch (error) {
      console.error(error);
    }
  }



    async function getData() {
      try {
        const response = await axios.get("/test2");
        setTableData(response.data);
        console.log(response.data);
        try {
          const response = await axios.get("http://192.168.1.254:5000/test2");
          setTableData(response.data);
          console.log(response.data);
        } catch (error) {
          console.error(error);
        }
      } catch (error) {
        console.error(error);
      }
    }


  useEffect(() => {
    getLan();
    getData();
    const interval = setInterval(()=>{
      getData();
    },10000)
    return ()=>clearInterval(interval);
  }, []);

  const check=()=>{
    if((saveDisableCheck.ip==true) || (saveDisableCheck.subnet==true) || (saveDisableCheck.ippoll1==true) || (saveDisableCheck.ippoll2==true) || (saveDisableCheck.gateway==true)){
      setSaveDisable(true);
    console.log(saveDisableCheck)
    }
    else {
      console.log(saveDisableCheck)
        setSaveDisable(false);

    }
  }


  const handleDHCPSelect = (e) => {
    setLanForm({...lanForm,dhcp:e.target.value});
  };

  const handleMac = (event) => {
    setLanForm({ ...lanForm, mac: event.target.value });
  };

  useEffect(()=>{
  check();
  },[saveDisableCheck])



  const handleIp = (event) => {
    setErrors({ ...errors,ip: "" });
    setLanForm({ ...lanForm, ip: event.target.value });

    var expression = /((^\s*((([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5]))\s*$)|(^\s*((([0-9A-Fa-f]{1,4}:){7}([0-9A-Fa-f]{1,4}|:))|(([0-9A-Fa-f]{1,4}:){6}(:[0-9A-Fa-f]{1,4}|((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){5}(((:[0-9A-Fa-f]{1,4}){1,2})|:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){4}(((:[0-9A-Fa-f]{1,4}){1,3})|((:[0-9A-Fa-f]{1,4})?:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){3}(((:[0-9A-Fa-f]{1,4}){1,4})|((:[0-9A-Fa-f]{1,4}){0,2}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){2}(((:[0-9A-Fa-f]{1,4}){1,5})|((:[0-9A-Fa-f]{1,4}){0,3}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){1}(((:[0-9A-Fa-f]{1,4}){1,6})|((:[0-9A-Fa-f]{1,4}){0,4}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(:(((:[0-9A-Fa-f]{1,4}){1,7})|((:[0-9A-Fa-f]{1,4}){0,5}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:)))(%.+)?\s*$))/;
    if (expression.test(event.target.value))
{
    setSaveDisableCheck({...saveDisableCheck,ip:false});
}
else
{
  setErrors({...errors, ip: "Invalid Ip" });
  setSaveDisableCheck({...saveDisableCheck,ip:true});
}
  };

  const handleSubnet = (event) => {
    setErrors({...errors, subnet: "" });
    setLanForm({ ...lanForm, subnet: event.target.value });

    var reg=/^(254|252|248|240|224|192|128)\.0\.0\.0|255\.(254|252|248|240|224|192|128|0)\.0\.0|255\.255\.(254|252|248|240|224|192|128|0)\.0|255\.255\.255\.(254|252|248|240|224|192|128|0)$/;
    if (reg.test(event.target.value))
    {
        setSaveDisableCheck({...saveDisableCheck,subnet:false});
    }
    else
    {
      setErrors({...errors, subnet: "Invalid Subnet" });
      setSaveDisableCheck({...saveDisableCheck,subnet:true});
    }
  };
  const handleIpPoll1 = (event) => {
    setErrors({...errors,ippoll1:""});
    setLanForm({ ...lanForm, ippool1: event.target.value });
    if(event.target.value>=0 && event.target.value<=255){
      setSaveDisableCheck({...saveDisableCheck,ippoll1:false});
    }
    else{
      setErrors({...errors, ippoll1: "Invalid Ip Pool" });
      setSaveDisableCheck({...saveDisableCheck,ippoll1:true});
    }

  };
  const handleIpPoll2 = (event) => {
    setErrors({...errors,ippoll2:""});
    setLanForm({ ...lanForm, ippool2: event.target.value });
    if(event.target.value>=0 && event.target.value<=255){
      setSaveDisableCheck({...saveDisableCheck,ippoll2:false});
    }
    else{
      setErrors({...errors, ippoll2: "Invalid Ip Pool" });
      setSaveDisableCheck({...saveDisableCheck,ippoll2:true});
    }
  };
  const handleGateway = (event) => {
    setErrors({...errors, gateway: "" });
    setLanForm({ ...lanForm, gateway: event.target.value });

    var exp = /((^\s*((([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5]))\s*$)|(^\s*((([0-9A-Fa-f]{1,4}:){7}([0-9A-Fa-f]{1,4}|:))|(([0-9A-Fa-f]{1,4}:){6}(:[0-9A-Fa-f]{1,4}|((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){5}(((:[0-9A-Fa-f]{1,4}){1,2})|:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3})|:))|(([0-9A-Fa-f]{1,4}:){4}(((:[0-9A-Fa-f]{1,4}){1,3})|((:[0-9A-Fa-f]{1,4})?:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){3}(((:[0-9A-Fa-f]{1,4}){1,4})|((:[0-9A-Fa-f]{1,4}){0,2}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){2}(((:[0-9A-Fa-f]{1,4}){1,5})|((:[0-9A-Fa-f]{1,4}){0,3}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(([0-9A-Fa-f]{1,4}:){1}(((:[0-9A-Fa-f]{1,4}){1,6})|((:[0-9A-Fa-f]{1,4}){0,4}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:))|(:(((:[0-9A-Fa-f]{1,4}){1,7})|((:[0-9A-Fa-f]{1,4}){0,5}:((25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)(\.(25[0-5]|2[0-4]\d|1\d\d|[1-9]?\d)){3}))|:)))?\s*$))/;
    if (exp.test(event.target.value))
{
    setSaveDisableCheck({...saveDisableCheck,gateway:false});
}
else
{
  setErrors({ ...errors,gateway: "Invalid Gateway" });
  setSaveDisableCheck({...saveDisableCheck,gateway:true});
}
  };
  const handleAddLAN = (e) => {
    e.preventDefault();
    console.log(lanForm);
    try{
      axios
      .post("/addlan", lanForm)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
    setOpen(true);
    try{
      axios
      .post("http://192.168.1.254:5000/addlan", lanForm)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
    setOpen(true);
    
    }catch(error) {
      console.error(error);
    }
    }catch(error) {
      console.error(error);
    }
  };
  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="lans-container">
        <div className="wan-cards">
          <img src={lan} className="lan-icon" />
          <h2 className="lan-titles">LAN</h2>
          <h6 className="lan-sub">Information about LAN</h6>
          <hr
            style={{
              marginLeft: 15,
              marginRight: 15,
              color: "#E0E5E5",
              marginTop: -20,
            }}
          />
        </div>
        <form onSubmit={handleAddLAN}>
          <div style={{ marginLeft: "15px" }}>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 5 }}
                >
                  MAC Address :{" "}
                </Typography>
              </Grid>
              <Grid>
                <TextField
                  size="small"
                  variant="outlined"
                  disabled
                  value={lanForm.mac}
                  onChange={handleMac}
                />
              </Grid>
            </Grid>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 15 }}
                >
                  IP Address :{" "}
                </Typography>
              </Grid>
              <Grid style={{ marginTop: "10px" }}>
                <TextField
                  size="small"
                  variant="outlined"
                  value={lanForm.ip}
                  onChange={handleIp}
                  error={errors?.ip}
                  helperText={errors?.ip}
                />
              </Grid>
            </Grid>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 15 }}
                >
                  Subnet Mask :
                </Typography>
              </Grid>
              <Grid style={{ marginTop: "10px" }}>
                <TextField
                  size="small"
                  variant="outlined"
                  value={lanForm.subnet}
                  onChange={handleSubnet}
                  error={errors?.subnet}
                  helperText={errors?.subnet}
                />
              </Grid>
            </Grid>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 15 }}
                >
                  IP Address Pool :{" "}
                </Typography>
              </Grid>
              <Grid style={{ marginTop: "10px" }}>
                <TextField
                  size="small"
                  variant="outlined"
                  value={lanForm.ippool1}
                  onChange={handleIpPoll1}
                  error={errors?.ippoll1}
                  helperText={errors?.ippoll1}
                />
              </Grid>
              <Grid style={{ marginTop: "10px", marginLeft: "10px" }}>
                <TextField
                  size="small"
                  variant="outlined"
                  value={lanForm.ippool2}
                  onChange={handleIpPoll2}
                  error={errors?.ippoll2}
                  helperText={errors?.ippoll2}

                />
              </Grid>
            </Grid>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 15 }}
                >
                  Default Gateway :{" "}
                </Typography>
              </Grid>
              <Grid style={{ marginTop: "10px" }}>
                <TextField
                  size="small"
                  variant="outlined"
                  value={lanForm.gateway}
                  onChange={handleGateway}
                  error={errors?.gateway}
                  helperText={errors?.gateway}
                />
              </Grid>
            </Grid>
            <Grid container>
              <Grid item xs={3}>
                <Typography
                  variant="body2"
                  className="lan-details"
                  gutterBottom
                  style={{ marginTop: 15 }}
                >
                  DHCP
                </Typography>
              </Grid>
              <RadioGroup
                aria-label="dhcp"
                name="dhcp1"
                value={lanForm.dhcp}
                onChange={handleDHCPSelect}
                row
              >
                <FormControlLabel
                  value="enable"
                  control={<Radio color="default" />}
                  label="Enable"
                />
                <FormControlLabel
                  value="disable"
                  control={<Radio color="default" />}
                  label="Disable"
                />
              </RadioGroup>
            </Grid>

            <Button
              type="submit"
              variant="contained"
              color="primary"
              className="sbmt"
              disabled={saveDisable}
            >
              <span style={{ color: "white", width: 85 }}>Save</span>
            </Button>
            {open ? (
              <Snackbar
                open={open}
                autoHideDuration={3000}
                onClose={handleClose}
                action={
                  <>
                    <div style={{ display: "flex", placeContent: "center" }}>
                    Saved Successfully
                    </div>
                  </>
                }
              ></Snackbar>
            ) : (
              ""
            )}
          </div>
        </form>
        <hr style={{marginLeft: 15, marginRight: 15, color:"#E0E5E5"}}></hr>

        <h4 className="lan-list">Client List</h4>
        
        {tableData ? (
          <div style={{margin: "-10px 16px 20px"}}>
          <div className="lan-scroll">
          <table className="lan-table">
            <thead>
              <tr className="lan-table">
                <th>Id.No</th>
                <th>Client Name</th>
                <th>MAC Address</th>
                <th>Assigned IP</th>
              </tr>
            </thead>
            <tbody style={{textAlign: "center"}}>
              {tableData.map((val, index) => {
                return (
                  <tr key={index}>
                    <td style={{padding: 8}}>{index + 1}</td>
                    <td>{val[2]}</td>
                    <td>{val[0]}</td>
                    <td>{val[1]}</td>
                  </tr>
                  
                );
              })}
            </tbody>
          </table>
          </div>
          </div>
        ) :( <div className= "lan-loader">
          <CircularProgress />
          </div>
        )}
      </div>
    </>
  );
};

export default LAN;
