import { useState, useEffect } from "react";
import axios from "axios";
import speedTestIcon from "../../assets/icons_v2/internet_or.svg";
import downIcon from "../../assets/icons_v2/down_or.svg";
import upIcon from "../../assets/icons_v2/up_or.svg";
import "./SpeedTest.css";
import ReactSpeedometer from "react-d3-speedometer";
import { Button, CircularProgress, Grid } from "@material-ui/core";
import Navbar from "../ARCASideNav/Navbar";

import LogoutPopup from "../../LogoutPopup";

const SpeedTest = () => {
  const [speedVal, setSpeedVal] = useState();
  const [upSpeedVal, setUpSpeedVal] = useState();
  //const [downloadValues,setDownloadValues] = useState({})
  const [individualOneDownload, setIndividualOneDownload] = useState();
  const [individualTwoDownload, setIndividualTwoDownload] = useState();
  const [individualOneUpload, setIndividualOneUpload] = useState();
  const [individualTwoUpload, setIndividualTwoUpload] = useState();
  const [individualThreeDownload, setIndividualThreeDownload] = useState();
  const [individualThreeUpload, setIndividualThreeUpload] = useState();
  const [individualFourDownload, setIndividualFourDownload] = useState();
  const [individualFourUpload, setIndividualFourUpload] = useState();
  const [individualFiveDownload, setIndividualFiveDownload] = useState();
  const [individualFiveUpload, setIndividualFiveUpload] = useState();
  const [individualSixDownload, setIndividualSixDownload] = useState();
  const [individualSixUpload, setIndividualSixUpload] = useState();
  const [individualSevenDownload, setIndividualSevenDownload] = useState();
  const [individualSevenUpload, setIndividualSevenUpload] = useState();
  const [individualEightDownload, setIndividualEightDownload] = useState();
  const [individualEightUpload, setIndividualEightUpload] = useState();
  const [toggleSpeed, setToggleSpeed] = useState(true);
  const [toggleButton, setToggleButton] = useState(true);
  const [threadToggleButton, setThreadToggleButton] = useState(true);
  const [threadToggle, setThreadToggle] = useState(true);
  const [loader,setLoader] = useState();
  const [upSpeedColor, setUpSpeedColor] = useState([
    "#0FFFFF",
    "#0FFFFF",
    "#0FFFFF",
    "#0FFFFF",
    "#0FFFFF",
  ]);
  const [downSpeedColor, setDownSpeedColor] = useState([
    "#EA8533",
    "#EA8533",
    "#EA822D",
    "#EF8029",
    "#EF8029",
  ]);

  const handleRunTest = () => {
    setLoader(1);
    setSpeedVal(0);
    setUpSpeedVal(0);
    setIndividualOneDownload(0);
    setIndividualTwoDownload(0);
    setIndividualThreeDownload(0);
    setIndividualFourDownload(0);
    setIndividualFiveDownload(0);
    setIndividualSixDownload(0);
    setIndividualSevenDownload(0);
    setIndividualEightDownload(0);
    async function getData() {
      try {
        const response = await axios.get("/downloadtest");
        const res = await axios.get("/uploadtest");
        //setDownloadValues({...downloadValues,one:response.data.result[0].aggregate});
        setSpeedVal(response.data.result[0].aggregate);
        setUpSpeedVal(res.data.result[0].aggregate);
        setIndividualOneDownload(response.data.result[0].wlan0);
        setIndividualTwoDownload(response.data.result[0].eth1);
        setIndividualOneUpload(res.data.result[0].wlan0);
        setIndividualTwoUpload(res.data.result[0].eth1);
        setIndividualThreeDownload(response.data.result[0].eth2);
        setIndividualThreeUpload(res.data.result[0].eth2);
        setIndividualFourDownload(response.data.result[0].eth3);
        setIndividualFourUpload(res.data.result[0].eth3);
        setIndividualFiveDownload(response.data.result[0].eth4);
        setIndividualFiveUpload(res.data.result[0].eth4);
        setIndividualSixDownload(response.data.result[0].eth5);
        setIndividualSixUpload(res.data.result[0].eth5);
        setIndividualSevenDownload(response.data.result[0].eth6);
        setIndividualSevenUpload(res.data.result[0].eth6);
        setIndividualEightDownload(response.data.result[0].eth7);
        setIndividualEightUpload(res.data.result[0].eth7);
      } catch (error) {
        console.error(error);
      }
    }
    getData();
  };
   useEffect(()=>{
    if(speedVal>0.0001){
      setLoader(2)
    }
   },[speedVal])


   async function handleSession(e){
     e.preventDefault();
     const res = axios.get('/abc');
     localStorage.setItem('res',JSON.stringify(res));
     console.log('res',res)
   }

  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="speedtest-container">
        <div className="speedometer-container">
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <img src={speedTestIcon} className="speedtest-icon" />
              <h2 className="aggregate">Aggregate Speed</h2>
            </Grid>
            <Grid xs={6}>
              <div style={{ marginTop: "30px", marginLeft: "160px" }}>
                <h6
                  style={{
                    color: "#2E3D5A",
                    fontFamily: "Montserrat-regular",
                    fontSize: "10px",
                    marginLeft: "75px",
                    marginRight: "15px",
                    cursor: "pointer",
                  }}
                >
                  <span
                    className={threadToggleButton ? "active" : ""}
                    onClick={() => {
                      setThreadToggle(true);
                      setThreadToggleButton(true);
                      console.log("Single Thread Called");
                    }}
                  >
                    SINGLE THREAD
                  </span>

                  <span
                    className={threadToggleButton ? "" : "active"}
                    style={{ marginLeft: 10 }}
                    onClick={() => {
                      setThreadToggle(false);
                      setThreadToggleButton(false);
                      console.log("Multi Thread Called");
                    }}
                  >
                    MULTI THREAD
                  </span>
                </h6>
              </div>
            </Grid>
          </Grid>
          <div style={{ marginLeft: "381px", marginTop: "5px" }}>
            <Button
            style={{paddingTop:5, paddingLeft:5, paddingRight:5, paddingBottom: 5}}
              variant="contained"
              color="primary"
              className="test-btn"
              onClick={handleRunTest}
            >
              <span style={{ color: "white", fontSize: 10 }}>Run Speedtest</span>
            </Button>
          </div>
          <hr
            style={{
              color:"#E0E5E5",
              marginLeft: "15px",
              marginRight: "15px",
              marginTop: "-37px",
            }}
          ></hr>
         
           <>
          <div className="speedometer">
            <div
            >
            <div style={{fontSize: "small", width: "257px",
                height: "257px",
                marginLeft: "120px"}}>
              <ReactSpeedometer
                fluidWidth={true}
                maxValue={100}
                value={speedVal}
                valueTextFontSize={22}
                labelFontSize={13}
                needleColor="white"
                segments={5}
                needleHeightRatio={0.7}
                ringWidth={25}
                segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                textColor="white"
                valueTextFontWeight="700"
              />
              </div>
              <div className="down-speed">
                <p>Download Speed</p>
              </div>
              <img
                className="down-icon"
                src={downIcon}
                alt="Down Icon"
                style={{ width: "20px", height: "20px", float: "left" }}
              />
              <p className="down-Mbps">Mbps</p>
            </div>
            <div>
            <div style={{fontSize: "small", width: "257px",
                height: "257px",
                marginLeft: "120px"}}>
              <ReactSpeedometer
                fluidWidth={true}
                maxValue={100}
                value={upSpeedVal}
                valueTextFontSize={22}
                labelFontSize={13}
                needleColor="white"
                ringWidth={25}
                needleHeightRatio={0.7}
                segments={5}
                segmentColors={upSpeedColor}
                textColor="white"
                valueTextFontWeight="700"
              />
              </div>
              <div className="up-speed">
                <p>Upload Speed</p>
              </div>
              <img
                className="up-icon"
                src={upIcon}
                alt="Up Icon"
                style={{ width: "20px", height: "20px", float: "left" }}
              />
              <p className="up-Mbps">Mbps</p>
            </div>
          </div>
          </>
        </div>
        <div className="inidividual-conatiner">
          <div style={{ display: "flex", marginTop: "-36px" }}></div>

          <hr
            style={{
              color:"#E0E5E5",
              marginLeft: "15px",
              marginRight: "15px",
              marginTop: "-22px",
            }}
          ></hr>
          <h6
            style={{
              color: "#2E3D5A",
              fontFamily: "Montserrat-regular",
              fontSize: "10px",
              marginLeft: "685px",
              cursor: "pointer",
              marginTop: "1px",
            }}
          >
            <span
              className={toggleButton ? "active" : ""}
              onClick={() => {
                setToggleSpeed(true);
                setToggleButton(true);
              }}
            >
              DOWNLOAD
            </span>
            <img
              className="down-icon-down"
              src={downIcon}
              alt="Down Icon"
              style={{ width: "20px", height: "20px", marginBottom: "-5px" }}
            />
            <span
              className={toggleButton ? "" : "active"}
              onClick={() => {
                setToggleSpeed(false);
                setToggleButton(false);
              }}
            >
              UPLOAD
            </span>
            <img
              className="up-icon-down"
              src={upIcon}
              alt="Up Icon"
              style={{ width: "20px", height: "20px", marginBottom: "-5px" }}
            />
          </h6>
           <div className="network-container">
            <div
              style={{
                width: "193px",
                height: "148px",
                marginLeft: "35px",
                marginRight: "20px",
              }}
            >
              <div
                style={{
                  width: "127px",
                  height: "127px",
                }}
              >
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed ? individualOneDownload : individualOneUpload
                  }
                  needleColor="white"
                  segments={5}
                  needleHeightRatio={0.5}
                  labelFontSize="9"
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="wifi-interface">WWAN</p>
                <p className="individual-mbps">Mbps</p>
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed ? individualFiveDownload : individualFiveUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-5">WAN 4</p>
                <p className="individual-mbps">Mbps</p>
              </div>
            </div>
            <div
              style={{ width: "193px", height: "148px", marginRight: "20px" }}
            >
              <div
                style={{
                  width: "127px",
                  height: "127px",
                }}
              >
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed ? individualTwoDownload : individualTwoUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-2">WAN 1</p>
                <p className="individual-mbps">Mbps</p>
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed ? individualSixDownload : individualSixUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-6">WAN 5</p>
                <p className="individual-mbps">Mbps</p>
              </div>
            </div>
            <div
              style={{ width: "193px", height: "148px", marginRight: "20px" }}
            >
              <div
                style={{
                  width: "127px",
                  height: "127px",
                }}
              >
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed
                      ? individualThreeDownload
                      : individualThreeUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-3">WAN 2</p>
                <p className="individual-mbps">Mbps</p>
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed
                      ? individualSevenDownload
                      : individualSevenUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-7">WAN 6</p>
                <p className="individual-mbps">Mbps</p>
              </div>
            </div>
            <div
              style={{ width: "193px", height: "148px", marginRight: "20px" }}
            >
              <div
                style={{
                  width: "127px",
                  height: "127px",
                }}
              >
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed ? individualFourDownload : individualFourUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p className="interface-4">WAN 3</p>
                <p className="individual-mbps">Mbps</p>
                <ReactSpeedometer
                  forceRender={true}
                  fluidWidth={true}
                  maxSegmentLabels={0}
                  maxValue={30}
                  value={
                    toggleSpeed
                      ? individualEightDownload
                      : individualEightUpload
                  }
                  needleColor="white"
                  segments={5}
                  labelFontSize="9"
                  needleHeightRatio={0.5}
                  ringWidth={15}
                  segmentColors={toggleSpeed ? downSpeedColor : upSpeedColor}
                  textColor="white"
                />
                <p
                  style={{
                    fontSize: "10px",
                    fontFamily: "Montserrat-light",
                    color: "#adb2b2",
                    marginTop: "-32px",
                    marginLeft: "49px",
                  }}
                >
                  Mbps
                </p>
                <p
                  style={{
                    color: "white",
                    fontSize: "10px",
                    marginLeft: "49px",
                    marginTop: "-7px",
                  }}
                >
                  WAN 7
                </p>
                {loader===1?
        <div className="test-loader">
         <CircularProgress/>
         </div>:""}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SpeedTest;
