import { useEffect, useState } from "react";
import axios from "axios";
import {
  Switch,
  Grid,
  Typography,
  CircularProgress,
  TextField,
  MenuItem,
  Button,
} from "@material-ui/core";
import nav_boost_or from "../../assets/icons_v2/nav_boost_or.svg";
import "./SpeedBoost.css";
import Navbar from "../ARCASideNav/Navbar";
import LogoutPopup from "../../LogoutPopup";

const SpeedBoost = () => {
  const [toggleBoost, setToggleBoost] = useState(true);
  const [boostData,setBoostData] = useState(null);
  const [enableStateStatic,setEnableStateStatic] = useState([{
    interface:"",
    isp:"",
    statusType:""
  }])
      //useeffect
      console.log(enableStateStatic)

  const toggler = (event) => {
    setToggleBoost(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/speedbooston").catch((err) => console.log(err));
    } else if (event.target.checked === false) {
      const resp = axios.get("/speedboostoff").catch((err) => console.log(err));
    }
  };

  async function getStatus() {
    try {
      const res = await axios.get("/speedbooststatus");
      setToggleBoost(res.data.result);
      const boostdata = await axios.get('/getspeedboost')
    setBoostData(boostdata.data.result);
    } catch (error) {
       console.log(error);
    }
  }


  async function getData() {
    try {
      const res = await axios.get("/getspeedboost");
    } catch (error) {
      console.error(error);
    }
  }
  useEffect(() => {
    getData();
    const interval = setInterval(()=>{
      getData();
    },10000)
    return ()=>clearInterval(interval);
  }, []);

  useEffect(() => {
    getStatus();
  }, []);

  useEffect(()=>{
    const res = axios.post('/setspeedboost',enableStateStatic)
    getStatus();
  },[enableStateStatic])

const [editStatus, setEditStatus] = useState({
    statusType: "",
  });
  console.log(editStatus);

  const handleSelect = (e) => {
    setEditStatus({ ...editStatus, statusType: e.target.value });
    //console.log(e.target.value);
  };


  // ----------- select drop down--------------
  const status = [
    {
      value: "enable",
      label: "Enable",
    },
    {
      value: "disable",
      label: "Disable",
    },
  ];

  // --------- Table Functions -------------
  function createData(name, isp, status) {
    return { name, isp, status };
  }

  const rows = [
    createData("Wan 1", "Airtel_Privatenet", "Enable"),
    createData("Wan 2", "Jio_Privatenet", "Enable"),
    createData("Wan 3", "BSNL_Privatenet", "Enable"),
  ];
  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="boost-container">
      <Grid container spacing={2}>
          <Grid item xs={9}>
            <div style={{ marginLeft: 10 }}>
              <img src={nav_boost_or} className="boost-icon" />
              <h1 className="heading">Speed Boost</h1>
              <h2 className="sbsub-heading">Information about Speed Boost</h2>
            </div>
          </Grid>
          <Grid xs={3}>
            <div style={{ marginTop: "25px" }}>
              <Typography component="div">
              <Grid item style={{marginLeft: -20, marginBottom: -32}} >Off</Grid>
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>
                    <Switch
                      checked={toggleBoost}
                      onChange={toggler}
                      color="primary"
                      name="speedboost"
                      inputProps={{ "aria-label": "primary checkbox" }}
                    />
                  </Grid>
                </Grid>
                <Grid item style={{marginLeft: 60, marginTop: -32}}>On</Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: 15, marginRight: 15, marginTop: -10, color:"#E0E5E5" }} />
        <div style={{ margin: '15px 15px' }}>
            {boostData ? (
             <table>
          <thead>
            <tr style={!toggleBoost ? {color:'#f3efef'}:{}}>
              <th style={{textAlign:"inherit"}}>Interface</th>
              <th style={{textAlign:"inherit"}}>ISP Name</th>
              <th style={{textAlign:"inherit"}}>Status</th>
            </tr>
          </thead>
          <tbody>
            {boostData?.map((item,index) => {
              return (
                <tr key={index} style={{ textAlign: '-webkit-center' }}>
                  <td className="speed-interface" style={!toggleBoost ? {color:'#e0e0e0'}:{}}>{item.interface[1]}</td>
                  <td className="speed-interface1" style={!toggleBoost ? {color:'#e0e0e0'}:{}}>{item.interface[0]}</td>
                  <td style={{padding:8}}>
                    {item.interface[2]?<Button 
                    size="small"
                    id="speedboost-btns"
                    variant="outlined"
                    color="secondary"
                    // style={{ marginLeft: "-65px" }}
                    disabled={!toggleBoost}
                    onClick={(e) => {
                          setEnableStateStatic({
                            interface:item.interface[1],
                            isp:item.interface[0],
                            statusType: !item.interface[2]
                          })
                          // const res = axios.post('/setenablespeedboost',enableStateStatic)

                        }}><span style={toggleBoost ?  { color: "#f50057",padding: "0px 12px" }:{ color: "#e0e0e0",padding: "0px 12px" }}>Disable</span></Button>:<Button  
                        size="small"
                              variant="outlined"
                              color="primary"
                              id="speedboost-btns"
                              // style={{ marginLeft: "-65px" }}
                              disabled={!toggleBoost}
                        onClick={(e) => {
                          setEnableStateStatic({
                            interface:item.interface[1],
                            isp:item.interface[0],
                            statusType: !item.interface[2]
                          })
                          // const res = axios.post('/setenablespeedboost',enableStateStatic)

                        }}><span
                        style={toggleBoost ? {
                          color: "#3f51b5",
                          padding: "0px 12px",
                        }:{color: '#e0e0e0',padding: "0px 12px"}}
                      >Enable</span></Button>}

                  </td>
                </tr>
              );
            })}
            
          </tbody>
        </table>
        
            ):(
        <div className="loader">
          <CircularProgress />
        </div>
            )}
            </div>
      </div>
    </>
  );
};

export default SpeedBoost;
