import './App.css';
import './index.css';
import {BrowserRouter,Route,Switch, Redirect} from "react-router-dom";
import Login from './components/Login/Login';
import Routes from './routes';


function App() {

  return (
  <>
    <BrowserRouter>
    <Switch>
      {
            JSON.parse(sessionStorage.getItem('isLoggedIn',true)) ?
              <Routes /> : <Login />
          }
    </Switch>
    </BrowserRouter>
    </>
  );
}

export default App;
