import { React, useEffect, useState } from "react";
import { NavLink, useHistory } from "react-router-dom";
import { Sidebardata } from "./Sidebardata";
import Logo from "../../assets/Icons/ARCA_Logo_white.png";
import Logo1 from "../../assets/Icons/ARCA_Logo_bg.png";
import Arca_logo from "../../assets/Icons/ARCA_Logo_bg_White_bold.svg";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import PowerSettingsNewIcon from "@material-ui/icons/PowerSettingsNew";
import axios from "axios";
import "./Navbar.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  sortIconDescending: {
    transform: "rotate(360deg)",
    cursor: "pointer",
    marginLeft: 5,
    marginTop: 7
  },
  appBar: {
    backgroundColor: "#EF8029",
    marginTop: "-8px",
  },
}));

function Navbar() {
  const classes = useStyles();
  let history = useHistory();
  const [user, setUser] = useState("arcauser");

  async function handleLogout(event){
    event.preventDefault();
    const res = await axios.get('/logout');
    console.log('response',res);
    if(res.data.data === false){
      sessionStorage.clear();
      history.push('/');
      window.location.reload()
    }
  }

  return (
    <>
      <div className={classes.root}>
        <AppBar position="static" className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" style={{marginTop: 5}}>{user}</Typography>
            <PowerSettingsNewIcon
              className={classes.sortIconDescending}
              onClick = {handleLogout}
            />
          </Toolbar>
        </AppBar>
      </div>
      <nav className="sidebar">
        <div>
          <img src={Arca_logo} className="figure" />
        </div>
        <ul className="sidebar_items">
          {Sidebardata.map((items, index) => (
            <li key={index} className={items.cname}>
              <NavLink
                to={items.link}
                className="menu_text"
                activeClassName="active"
              >
                {items.icon}
                <span className="nav-items">{items.title}</span>
              </NavLink>
            </li>
          ))}
        </ul>
        <p className="jaitra-logo">© Jaitra Inc</p>;
      </nav>
    </>
  );
}
export default Navbar;
