import { React, useState, useEffect } from "react";
import axios from "axios";
import Navbar from "../ARCASideNav/Navbar";
import CloseIcon from "@material-ui/icons/Close";
import VPNLogo from "../../assets/icons_v2/nav_vpn_or.svg";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  Grid,
  CardActions,
  CardContent,
  Button,
  Typography,
  TextField,
  Badge,
  Modal,
  MenuItem,
  Switch,
  CircularProgress,
  Snackbar,
} from "@material-ui/core";
import green_dot from "../../assets/Icons/green_dot.svg";
import red_dot from "../../assets/Icons/red_dot.svg";
import "./VPNSettings.css";
import LogoutPopup from "../../LogoutPopup";
import { InputAdornment, IconButton } from "@material-ui/core";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}
function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();
  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}
const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
  card: {
    maxWidth: 350,
    boxShadow: "0 2px 4px 0 rgba(0, 0, 0, 0.3)",
  },
  media: {
    height: 300,
  },
  cardGrid: {
    flexGrow: 1,
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    position: "absolute",
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  paper1: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

const wirelessConnection = [
  {
    value: "Manual",
    label: "Manual",
  },
  {
    value: "Config file",
    label: "Config file",
  },
];
function VPNSettings() {
  const classes = useStyles();
  const [modalStyle] = useState(getModalStyle);
  const [upload, setUpload] = useState();
  const [openPopUp, setOpenPopUp] = useState();
  const [fileName, setFileName] = useState();
  const [disable, setDisable] = useState(false);
  const [popUpVal, setPopUpVal] = useState();
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const handleMouseDownPassword = () => setShowPassword(!showPassword);
  const [addNetworkForm, setAddNetworkForm] = useState({
    type: "",
    networkName: "",
    serverAddress: "",
    protocol: "",
    port: "",
  });

  const [addNetworkForm2, setAddNetworkForm2] = useState({
    type: "",
    networkName: "",
    username: "",
    password: "",
    file: null,
    cardno: "",
  });
  const [state, setState] = useState({
    checkedA: true,
    checkedB: true,
  });
  const [val, setVal] = useState();
  const [dummy, setDummy] = useState({});
  const [dummi, setDummi] = useState({});
  const handleVal = (e) => {
    setVal(e.target.value);
  };
  const submitNetworkForm = (e) => {
    console.log("submit Edit Network form");
    const res = axios.post("/editvpn", addNetworkForm);
    closeModal();
    window.location.reload();
  };
  const handleClose = () => {
    setOpenPopUp(false);
  };
  const submitNetworkForm2 = (e) => {
    console.log("submit Edit Network form");
    const res = axios.post("/editvpn", addNetworkForm2);
    closeModal();
    window.location.reload();
  };

  const handleUpload = () => {
    document.getElementById("getFile").click();
  };

  const loadFile = (e) => {
    const formData = new FormData();
    console.log(e.target.files[0]);
    setFileName(e.target.files[0].name);
    console.log(e.target.files[0].name)
    if (upload === 1) {
      formData.append("card1", e.target.files[0]);
    } else if (upload === 2) {
      formData.append("card2", e.target.files[0]);
    }
    async function post() {
      const res = await axios.post("/editupload", formData);
      if (res.data.result === "Upload Successfull") {
        setOpenPopUp(true);
        setPopUpVal("Uploaded Successfully"); 
      } else if (res.data.result === "Invalid File") {
        setOpenPopUp(true);
        setPopUpVal("Choose a file must end in .ovpn");
        setDisable(true)
      }
    }
    post();
  };

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
    if (event.target.checked === true) {
      const res = axios.get("/vpnon").catch((err) => console.log(err));
    } else if (event.target.checked === false) {
      const resp = axios.get("/vpnoff").catch((err) => console.log(err));
    }
  };

  const handleNetworkName = (e) => {
    setAddNetworkForm({ ...addNetworkForm, networkName: e.target.value });
  };
  const handleServerAddress = (e) => {
    setAddNetworkForm({ ...addNetworkForm, serverAddress: e.target.value });
  };
  const handleProtocol = (e) => {
    setAddNetworkForm({ ...addNetworkForm, protocol: e.target.value });
  };
  const handlePort = (e) => {
    setAddNetworkForm({ ...addNetworkForm, port: e.target.value });
  };
  const handleNetworkName2 = (e) => {
    setAddNetworkForm2({ ...addNetworkForm2, networkName: e.target.value });
  };
  const handleUsername = (e) => {
    setAddNetworkForm2({ ...addNetworkForm2, username: e.target.value });
  };
  const handlePassword = (e) => {
    setAddNetworkForm2({ ...addNetworkForm2, password: e.target.value });
  };
  const [modalIsOpen, setIsOpen] = useState(false);
  function openEditModal() {
    console.log("WAN1");
    setIsOpen(true);
  }
  function closeModal() {
    setIsOpen(false);
  }

  function openModal1() {
    setIsOpen(true);
    console.log(dummy.nname);
    setAddNetworkForm({
      ...addNetworkForm,
      networkName: dummy.nname,
      username: dummy.uname,
      cardno: 1,
    });
    setAddNetworkForm2({
      ...addNetworkForm2,
      networkName: dummy.nname,
      username: dummy.uname,
      cardno: 1,
    });
    setUpload(1);
  }
  function openModal2() {
    setIsOpen(true);
    console.log(dummi.nname);
    setAddNetworkForm({
      ...addNetworkForm,
      networkName: dummi.nname,
      username: dummi.uname,
      cardno: 2,
    });
    setAddNetworkForm2({
      ...addNetworkForm2,
      networkName: dummi.nname,
      username: dummi.uname,
      cardno: 2,
    });
    setUpload(2);
  }

  const [vpnOne, setVpnOne] = useState({});
  const [vpnTwo, setVpnTwo] = useState({});

  async function getData() {
    try {
      const response = await axios.get("/vpn1ip");
      setVpnOne(response.data.result);
      const res = await axios.get("/vpn2ip");
      setVpnTwo(res.data.result);
      const resp = await axios.get("/vpnstatus");
      setState({ ...state, checkedA: resp.data.result });
      const pan = await axios.get("/getvpn1");
      setDummy({
        nname: pan.data.result.vpn1_name,
        uname: pan.data.result.username,
      });
      const pant = await axios.get("/getvpn2");
      setDummi({
        nname: pant.data.result.vpn1_name,
        uname: pant.data.result.username,
      });
    } catch (error) {
      console.error(error);
    }
  }

  useEffect(() => {
    getData();
    const interval = setInterval(() => {
      getData();
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  const vpn1Disconnect = () => {
    async function get() {
      const res = await axios.get("/vpn1disconnect");
      console.log(res);
    }
    get();
    window.location.reload();
  };
  const vpn1Connect = () => {
    async function get() {
      const res = await axios.get("/vpn1connect");
      console.log(res);
    }
    get();
    window.location.reload();
  };

  const vpn2Disconnect = () => {
    async function get() {
      const res = await axios.get("/vpn2disconnect");
      console.log(res);
    }
    get();
    window.location.reload();
  };
  const vpn2Connect = () => {
    async function get() {
      const res = await axios.get("/vpn2connect");
      console.log(res);
    }
    get();
    window.location.reload();
  };

  return (
    <>
      {modalIsOpen && (
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={modalIsOpen}
          onClose={closeModal}
        >
          <div style={modalStyle} className={classes.paper}>
            <div className="wan-container">
              <span>
                <Grid container spacing={2}>
                  <Grid item xs={10}>
                    <Typography
                      variant="h6"
                      style={{ fontSize: 15, fontFamily: "MONTSERRAT-LIGHT" }}
                    >
                      Edit VPN
                    </Typography>
                  </Grid>
                  <Grid>
                    <CloseIcon
                      onClick={closeModal}
                      style={{ marginTop: "7px", cursor:"pointer",color:"#A5AFAF", marginLeft:"14px" }}
                    />
                  </Grid>
                </Grid>
                <hr />
              </span>
              <form>
                <div style={{ marginTop: "10px" }}>
                  <TextField
                    id="standard-select-currency-native"
                    select
                    label="Type"
                    value={val}
                    defaultValue={"Config file"}
                    onChange={handleVal}
                    variant="outlined"
                    size="small"
                  >
                    {wirelessConnection.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </TextField>
                </div>
                {val === "Manual" ? (
                  <div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Network Name"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm.networkName}
                        onChange={handleNetworkName}
                        disabled
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Server Address"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm.serverAddress}
                        onChange={handleServerAddress}
                        disabled
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Protocol Type"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm.protocol}
                        onChange={handleProtocol}
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Port"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm.port}
                        onChange={handlePort}
                        disabled
                      />
                    </div>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      onClick={submitNetworkForm}
                      style={{ marginTop: "34px", width: 85 }}
                      disabled
                    >
                      <span style={{ color: "white" }}>Save</span>
                    </Button>
                  </div>
                ) : (
                  <div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Network Name"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm2.networkName}
                        onChange={handleNetworkName2}
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="Username"
                        id="outlined-size-small"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm2.username}
                        onChange={handleUsername}
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <TextField
                        label="password"
                        id="outlined-size-small"
                        type="password"
                        variant="outlined"
                        size="small"
                        value={addNetworkForm2.password}
                        onChange={handlePassword}
                        type={showPassword ? "text" : "password"}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                aria-label="toggle password visibility"
                                onClick={handleClickShowPassword}
                                onMouseDown={handleMouseDownPassword}
                                edge="end"
                              >
                                {showPassword ? (
                                  <Visibility />
                                ) : (
                                  <VisibilityOff />
                                )}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />
                    </div>
                    <div style={{ marginTop: "10px" }}>
                      <Button
                        variant="contained"
                        color="default"
                        startIcon={<CloudUploadIcon />}
                        onClick={handleUpload}
                        style={{ width: "98px" }}
                      >
                        Upload
                      </Button>
                      <input
                        type="file"
                        onChange={loadFile}
                        id="getFile"
                        style={{ display: "none" }}
                        accept=".ovpn"
                        required
                       />
                      <div className="vpn-file">{fileName}</div>
                    </div>
                    <div>
                      <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        onClick={submitNetworkForm2}
                        disabled={disable}
                        // disabled={true}
                        // disabled={setPopUpVal("Choose a file must end in .ovpn")}
                        style={{ marginTop: "34px", width: 85 }}
                      >
                        <span style={{ color: "white" }}>Save</span>
                      </Button>
                    </div>
                  </div>
                )}
              </form>
            </div>
          </div>
        </Modal>
      )}
      {openPopUp ? (
        <Snackbar
          open={openPopUp}
          autoHideDuration={3000}
          onClose={handleClose}
          action={
            <>
              <div style={{ display: "flex", placeContent: "center" }}>
                {popUpVal}
              </div>
            </>
          }
        ></Snackbar>
      ) : (
        ""
      )}
      <Navbar />
      <LogoutPopup />
      <div className="vpn-container">
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <img src={VPNLogo} className="vpn-icon" />
            <h2 className="vpn-titles">Jaitra VPN</h2>
            <h6 className="vpn-sub">List of VPN Instances</h6>
          </Grid>
          <Grid item xs={6}>
            <div style={{ marginTop: "20px", marginLeft: 125 }}>
              <Typography component="div">
              <Grid item style={{marginLeft: -20, marginBottom: -32}} >Off</Grid>
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>
                    <Switch
                      checked={state.checkedA}
                      onChange={handleChange}
                      color="primary"
                      name="checkedA"
                      inputProps={{ "aria-label": "primary checkbox" }}
                    />
                  </Grid>
                </Grid>
                <Grid item style={{marginLeft: 60, marginTop: -32}}>On</Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr
          style={{
            marginLeft: 15,
            marginRight: 17,
            marginTop: -20,
            color: "#E0E5E5",
          }}
        />
        {vpnTwo.name ? (
          <div style={{ marginLeft: 7 }}>
            <Grid container spacing={2}>
              <Grid item xs={4}>
                <div className="card-1">
                  <Card style={{ width: "218px", marginLeft: "13px" }}>
                    <CardContent
                      className="card-content"
                      style={
                        !state.checkedA
                          ? { paddingBottom: "0px", color: "#e0e0e0" }
                          : { paddingBottom: "0px" }
                      }
                    >
                      <Grid container spacing={2}>
                        <Grid item xs={11}>
                          <Typography
                            variant="h5"
                            component="h2"
                            className="vpncard-heading"
                          >
                            VPN 1
                          </Typography>
                        </Grid>
                        <Grid>
                          {/* <img src={green_dot} className="green-dot" /> */}
                          {vpnOne.status ? (
                            <img src={green_dot} className="green-dot" />
                          ) : (
                            <img src={red_dot} className="green-dot" />
                            // <Badge
                            //   color="secondary"
                            //   variant="dot"
                            //   className="green-dot"
                            // />
                          )}
                        </Grid>
                      </Grid>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{
                          fontSize: 12,
                          fontFamily: "Montserrat-semibold",
                        }}
                      >
                        {vpnOne.name}
                      </Typography>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{ fontSize: 10, fontFamily: "Montserrat-light" }}
                      >
                        Type : {vpnOne.type}
                      </Typography>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{ fontSize: 10, fontFamily: "Montserrat-light" }}
                      >
                        IP Address : {vpnOne.ip}
                      </Typography>
                    </CardContent>
                    <CardActions
                      className="card-btn"
                      style={{ paddingTop: "15px" }}
                    >
                      <Grid container spacing={2}>
                        <Grid item xs={4}>
                          <Button
                            size="small"
                            variant="outlined"
                            onClick={openModal1}
                            disabled={!state.checkedA}
                          >
                            <span
                              style={
                                !state.checkedA ? { color: "#e0e0e0" } : {}
                              }
                            >
                              Edit
                            </span>
                          </Button>
                        </Grid>
                        <Grid item xs={6}>
                          {vpnOne.status ? (
                            <Button
                              size="small"
                              variant="outlined"
                              color="secondary"
                              style={{ marginLeft: "15px" }}
                              onClick={vpn1Disconnect}
                              disabled={!state.checkedA}
                            >
                              <span
                                style={
                                  state.checkedA
                                    ? { color: "#f50057", padding: "0px 5px" }
                                    : { color: "#e0e0e0", padding: "0px 5px" }
                                }
                              >
                                Disconnect
                              </span>
                            </Button>
                          ) : (
                            <Button
                              size="small"
                              variant="outlined"
                              color="primary"
                              style={{ marginLeft: "20px" }}
                              onClick={vpn1Connect}
                              disabled={!state.checkedA}
                            >
                              <span
                                style={
                                  state.checkedA
                                    ? { color: "#3f51b5", padding: "0px 13px" }
                                    : { color: "#e0e0e0", padding: "0px 13px" }
                                }
                              >
                                Connect
                              </span>
                            </Button>
                          )}
                        </Grid>
                      </Grid>
                    </CardActions>
                  </Card>
                </div>
              </Grid>
              <Grid item xs={4}>
                <div className="card-1">
                  <Card style={{ width: "218px", marginLeft: "65px" }}>
                    <CardContent
                      className="card-content"
                      style={
                        !state.checkedA
                          ? { paddingBottom: "0px", color: "#e0e0e0" }
                          : { paddingBottom: "0px" }
                      }
                    >
                      <Grid container spacing={2}>
                        <Grid item xs={11}>
                          <Typography
                            variant="h5"
                            component="h2"
                            className="vpncard-heading"
                          >
                            VPN 2
                          </Typography>
                        </Grid>
                        <Grid>
                          {/* <img src={green_dot} className="green-dot" /> */}
                          {vpnTwo.status ? (
                            <img src={green_dot} className="green-dot" />
                          ) : (
                            <Badge
                              color="secondary"
                              variant="dot"
                              className="green-dot"
                            />
                          )}
                        </Grid>
                      </Grid>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{
                          fontSize: 12,
                          fontFamily: "Montserrat-semibold",
                        }}
                      >
                        {vpnTwo.name}
                      </Typography>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{ fontSize: 10, fontFamily: "Montserrat-light" }}
                      >
                        Type : {vpnTwo.type}
                      </Typography>
                      <Typography
                        variant="body2"
                        component="p"
                        style={{ fontSize: 10, fontFamily: "Montserrat-light" }}
                      >
                        IP Address : {vpnTwo.ip}
                      </Typography>
                    </CardContent>
                    <CardActions
                      className="card-btn"
                      style={{ paddingTop: "15px" }}
                    >
                      <Grid container spacing={2}>
                        <Grid item xs={4}>
                          <Button
                            size="small"
                            variant="outlined"
                            onClick={openModal2}
                            disabled={!state.checkedA}
                          >
                            <span
                              style={
                                !state.checkedA ? { color: "#e0e0e0" } : {}
                              }
                            >
                              Edit
                            </span>
                          </Button>
                        </Grid>
                        <Grid item xs={6}>
                          {vpnTwo.status ? (
                            <Button
                              size="small"
                              variant="outlined"
                              color="secondary"
                              style={{ marginLeft: "15px" }}
                              onClick={vpn2Disconnect}
                              disabled={!state.checkedA}
                              // style={!state.checkedA ? {color:'#e0e0e0', marginLeft: "20px"}:{marginLeft: "20px"}}
                            >
                              <span
                                style={
                                  state.checkedA
                                    ? { color: "#f50057", padding: "0px 5px" }
                                    : { color: "#e0e0e0", padding: "0px 5px" }
                                }
                              >
                                Disconnect
                              </span>
                            </Button>
                          ) : (
                            <Button
                              size="small"
                              variant="outlined"
                              color="primary"
                              style={{ marginLeft: "20px" }}
                              onClick={vpn2Connect}
                              disabled={!state.checkedA}
                              // style={!state.checkedA ? {color:'#e0e0e0', marginLeft: "25px"}:{marginLeft: "25px"}}
                            >
                              <span
                                style={
                                  state.checkedA
                                    ? { color: "#3f51b5", padding: "0px 13px" }
                                    : { color: "#e0e0e0", padding: "0px 13px" }
                                }
                              >
                                Connect
                              </span>
                            </Button>
                          )}
                        </Grid>
                      </Grid>
                    </CardActions>
                  </Card>
                </div>
              </Grid>
            </Grid>
          </div>
        ) : (
          <div className="vpn-loader">
            <CircularProgress />
          </div>
        )}
      </div>
    </>
  );
}

export default VPNSettings;
