import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Logo from "../../assets/Icons/ARCA_Logo_white.png";
import { Container, Button, Grid, TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import "./Login.css";
import { InputAdornment, IconButton } from "@material-ui/core";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";


const useStyles = makeStyles((theme) => ({
  container: {
    padding: theme.spacing(5),
  },
}));

const initialState = {
  username: "",
  password: "",
};

const Login = ({}) => {
  const classes = useStyles();
  let history = useHistory();
  const [loginReqData, setLoginReqData] = useState(initialState);
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const handleMouseDownPassword = () => setShowPassword(!showPassword);
  const [disable, setDisable] = React.useState(false);
  const [attempt, setAttempt] = useState(0);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    setAttempt(JSON.parse(window.localStorage.getItem('attempt')));
  }, []);

  useEffect(() => {
    window.localStorage.setItem('attempt', attempt);
  }, [attempt]);

  const onLoginReqDataChange = (e) => {
    setLoginReqData({
      ...loginReqData,
      [e.target.name]: e.target.value,
    });
  };


  async function handleLogin(event) {
    event.preventDefault();
    const res = await axios.post("/login", loginReqData);
    sessionStorage.setItem("isLoggedIn", JSON.stringify(res.data.data));
    sessionStorage.setItem("loginResponse", JSON.stringify(res));
    const login = res.data.data;
    if (login === true) {
      history.push("/dashboard");
      window.location.reload();
    } else {
      setAttempt((prevValue) => prevValue + 1);
      setLoginReqData ({
        ...loginReqData,
        attempt:attempt
      })
      console.log("attempt",attempt);
      setErrorMessage("Incorrect username or password. User has 2 more attempts left.");
      if (attempt === 1) {
      setErrorMessage(
        "Incorrect username or password. User has "+attempt+" more attempts left.");
      }
      if (attempt >= 2) {
        setTimeout(setDisable(true), 60000);
        setErrorMessage(
          "Incorrect username or password was entered too many times. Try again in 60 seconds. "
        );
        setTimeout(() => {
          window.location.reload();
          localStorage.clear();
        }, 60000);
      }      
    } 
  }

  useEffect(() => {
    const res =  axios.post("/login", loginReqData);
  }, [attempt,loginReqData])


  const [textConfig, setTextConfig] = useState({
    variant: "outlined",
    name: "password",
    type: "password",
    label: "Password",
  });

  return (
    <div className="df-login-container">
      <div className="login-container">
        <Container className={classes.container} maxWidth="xs">
          <form onSubmit={handleLogin}>
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
              spacing={0}
              mt-3
            >
              <img src={Logo} style={{ width: "130px", height: "30px" }} />
            </Grid>
            <div className="arca-login-form">
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Username"
                        name="username"
                        size="small"
                        variant="outlined"
                        value={loginReqData.username}
                        onChange={(e) => onLoginReqDataChange(e)} 
                        autoComplete="off"
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        size="small"
                        {...textConfig}
                        value={loginReqData.password}
                        onChange={(e) => onLoginReqDataChange(e)}
                        autoComplete="off"
                        type={showPassword ? "text" : "password"}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                aria-label="toggle password visibility"
                                onClick={handleClickShowPassword}
                                onMouseDown={handleMouseDownPassword}
                                edge="end"
                              >
                                {showPassword ? (
                                  <Visibility />
                                ) : (
                                  <VisibilityOff />
                                )}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <Button
                    color="primary"
                    fullWidth
                    type="submit"
                    variant="contained"
                    onClick={handleLogin}
                    disabled={disable ||(loginReqData.username==='') || (loginReqData.password==='')}
                    style={{ marginBottom: 10 }}
                  >
                    <span style={{ color: "white", fontWeight: "bolder" }}>
                      Login
                    </span>
                  </Button>
                  <div className="df-error-section">
                    {errorMessage && (
                      <div className="df-error-message">{errorMessage}</div>
                    )}
                  </div>
                  <p className="jaitra">© Jaitra Inc</p>
                </Grid>
              </Grid>
            </div>
          </form>
        </Container>
        
      </div>
    </div>
  );
};

export default Login;
