import React, { useState } from "react";
import "./Payments.css";
import Navbar from "../ARCASideNav/Navbar";
import { Grid, Typography } from "@material-ui/core";
import nav_subscription_or from "../../assets/icons_v2/nav_subscription_or.svg";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import LogoutPopup from "../../LogoutPopup";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData("26 june 2021", "speed control", "26 july 2021", "paid", 2600),
  createData("26 may 2021", "speed control", "26 july 2021", "not paid", 2600),
];

const Payments = () => {
  const classes = useStyles();
  const subscriptionData = {
    plan: "Premium plan",
    ValidTill: "11 Jul 2021",
    Amount: "RS/- 2600",
  };
  const [subscription, setSubscription] = useState(subscriptionData);
  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="payment-container">
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <img src={nav_subscription_or} className="payment-icon" />
            <h2 className="pay-titles">Subscription</h2>
            <h6 className="pay-sub">List of Invoices</h6>
            <hr
              style={{
                marginLeft: 15,
                marginRight: 10,
                marginTop: -10,
                color: "#E0E5E5",
              }}
            />
          </Grid>
          <Grid item xs={4} style={{ padding: 0, margin: "5px 0px 10px 25px" }}>
            <Typography
              variant="caption"
              display="block"
              gutterBottom
              style={{ color: "#4D545A" }}
            >
              Plan
            </Typography>
            <Typography variant="h6" gutterBottom style={{ color: "#EF8029" }}>
              {subscription.plan}
            </Typography>
          </Grid>
          <Grid item xs={4} style={{ padding: 0 }}>
            <Typography
              variant="caption"
              display="block"
              gutterBottom
              style={{ color: "#4D545A" }}
            >
              Valid Till
            </Typography>
            <Typography variant="h6" gutterBottom>
              {subscription.ValidTill}
            </Typography>
          </Grid>
          <Grid item xs={3} style={{ padding: 0 }}>
            <Typography
              variant="caption"
              display="block"
              gutterBottom
              style={{ color: "#4D545A" }}
            >
              Amount
            </Typography>
            <Typography variant="h6" gutterBottom style={{fontWeight:700}}>
              {subscription.Amount}
            </Typography>
          </Grid>
        </Grid>
      </div>

      <div className="payment-container" style={{ marginTop: "170px" }}>
        <Grid container>
          <Grid item xs={10}>
            <img src={nav_subscription_or} className="payment-icon" />
            <h2 className="titles">Payment History</h2>
            <h6 className="sub">List of Invoices</h6>
          </Grid>
        </Grid>

        <TableContainer component={Paper}>
          <Table
            className={classes.table}
            size="small"
            aria-label="a dense table"
          >
            <TableHead>
              <TableRow>
                <TableCell >Bill Date</TableCell>
                <TableCell >Description</TableCell>
                <TableCell >Due date</TableCell>
                <TableCell >Status</TableCell>
                <TableCell >Total</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <TableRow key={row.name}>
                  <TableCell >{row.name}</TableCell>
                  <TableCell >{row.calories}</TableCell>
                  <TableCell >{row.fat}</TableCell>
                  <TableCell >{row.carbs}</TableCell>
                  <TableCell >{row.protein}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </>
  );
};

export default Payments;
