import { React, useEffect, useState } from "react";
import axios from "axios";
import devicesIcon from "../../assets/Icons/db_devices_02.svg";
import routerIcon from "../../assets/Icons/db_router_02.svg";
import downIcon from "../../assets/icons_v2/router_or.svg";
import internet_status_or from "../../assets/icons_v2/internet_status_or.svg";
import db_internet from "../../assets/Icons/db_internet_02.svg";
import db_greenline from "../../assets/Icons/db_greenline.svg";
import db_redline from "../../assets/Icons/db_redline.svg";
import loadBalanceIcon from "../../assets/Icons/load_balance.svg";
import NetworkWorkIcon from "../../assets/Icons/NAS.svg";
import green_dot from "../../assets/Icons/green_dot.svg";
import Navbar from "../ARCASideNav/Navbar";
import vpnIcon from "../../assets/Icons/nav_vpn.svg";
import speedBoostIcon from "../../assets/Icons/nav_boost.svg";
import parentalIcon from "../../assets/Icons/nav_parental.svg";
import { Grid, Typography, CircularProgress } from "@material-ui/core";
import LogoutPopup from "../../LogoutPopup";
import "./DashBoard.css";

const DashBoard = () => {
  const [routerName, setRouterName] = useState();
  const [systemInfo, setSystemInfo] = useState({});
  const [dongles, setDongles] = useState();
  const [deviceCon, setDeviceCon] = useState("");
  const [vpnStat, setVpnStat] = useState();
  const [speedBoostStat, setSpeedBoostStat] = useState();
  const [loadBalancingStat, setLoadBalancingStat] = useState();
  const [lineStat, setLineStat] = useState();
  const [parentalControlStatus, setParentalControlStatus] = useState();
  const [deviceConStatus, setDeviceConStatus] = useState();
  const [networkStat, setNetworkStat] = useState();

  async function getData() {
    try {
      const response = await axios.get("/api");
      const res = await axios.get("/version");
      const resp = await axios.get("/dongles");
      const respon = await axios.get("/devicecon");
      const vpnstatus = await axios.get("/vpnstatus");
      const speedbooststatus = await axios.get("/speedbooststatus");
      const loadbalancingstatus = await axios.get("/loadbalancingstatus");
      const linestatus = await axios.get("/linestatus");
      const parentalcontrolstatus = await axios.get("/parentstatus");
      const networkstatus = await axios.get("/nasstatus")
      setRouterName(response.data.title);
      setSystemInfo(res.data.result);
      setDongles(resp.data.result);
      setDeviceCon(respon.data.result);
      setDeviceConStatus(respon.data.status);
      setVpnStat(vpnstatus.data.result);
      setSpeedBoostStat(speedbooststatus.data.result);
      setLoadBalancingStat(loadbalancingstatus.data.result);
      setLineStat(linestatus.data.result);
      setParentalControlStatus(parentalcontrolstatus.data.result);
      setNetworkStat(networkstatus.data.result);
      try{
        const respon = await axios.get("http://192.168.1.254:5000/devicecon");
        setDeviceCon(respon.data.result);
        setDeviceConStatus(respon.data.status);
      }catch (error) {
        console.error(error);
      }
    } catch (error) {
      console.error(error);
    }
  }

  useEffect(() => {
    getData();
    const interval = setInterval(() => {
      getData();
    }, 10000)
    return () => clearInterval(interval);
  }, []);

  const overViewData = [
    {
      overviewName: "Load Balancer",
      overview: loadBalancingStat,
      overviewIcon: loadBalanceIcon,
    },
    {
      overviewName: "Speed Boost",
      overview: speedBoostStat,
      overviewIcon: speedBoostIcon,
    },
    {
      overviewName: "VPN Settings",
      overview: vpnStat,
      overviewIcon: vpnIcon,
    },

    {
      overviewName: "Parental Control",
      overview: parentalControlStatus,
      overviewIcon: parentalIcon,
    },
    {
      overviewName: "Network Storage",
      overview: networkStat,
      overviewIcon: NetworkWorkIcon,
    },
  ];

  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="dashboard-container">
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <img src={internet_status_or} className="dash-logo" />
            <p className="titles">Internet Status</p>
            <h6 className="sub">Information about available network</h6>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: 15, marginRight: 17, marginTop: "-25px", color: "#E0E5E5" }} />
        {routerName ? <>
          <Grid container spacing={2} style={{ marginLeft: 10 }}>
            <Grid item xs={2}>
              <img className="dongal" src={db_internet} alt="Up Icon" />
            </Grid>
            <Grid item xs={2} style={{ paddingLeft: 15 }}>
              {lineStat ? (
                <img className="green-line" src={db_greenline} />
              ) : (
                <img className="green-line" src={db_redline} />
              )}
            </Grid>
            <Grid item xs={2}>
              <img className="router" src={routerIcon} alt="Router Icon" />
            </Grid>
            <Grid item xs={2}>
              {deviceConStatus ? (<img className="green-line" src={db_greenline} />) : (
                <img className="green-line" src={db_redline} />)}
            </Grid>
            <Grid item xs={2}>
              <img className="device" src={devicesIcon} alt="Devices Icon" />
            </Grid>
          </Grid>
          <Grid container spacing={2} style={{ marginLeft: 10, marginTop: 10 }}>
            <Grid item xs={4}>
              <p className="dongle-internet">{dongles} Internet Source(s)</p>
            </Grid>
            <Grid item xs={4}>
              <p className="dongle-router">{routerName}</p>
            </Grid>
            <Grid item xs={4}>
              <p className="dongle-devices">{deviceCon} Device(s) Connected</p>
            </Grid>
          </Grid>
        </> : <div className="dashboard-loader">
          <CircularProgress />
        </div>}
      </div>
      <div className="dashboard-system-container">
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <img src={downIcon} className="lan-icon" />
            <h2 className="lan-titles">System</h2>
            <h6 className="lan-sub">Information about router</h6>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: 15, marginRight: 17, marginTop: "-25px", color: "#E0E5E5" }} />
        {systemInfo.hardwareVersion ?
          <>
            <Grid container spacing={2} style={{ marginLeft: 10 }}>
              <Grid item xs={6}>
                <h6 className="system-data">Model</h6>
                <p className="api-data">
                  {systemInfo.model ? systemInfo.model : "-----"}
                </p>
              </Grid>
              <Grid item xs={6}>
                <h6 className="system-data">Hardware Version</h6>
                <p className="api-data">
                  {systemInfo.hardwareVersion
                    ? systemInfo.hardwareVersion
                    : "-----"}
                </p>
              </Grid>
            </Grid>
            <Grid container spacing={2} style={{ marginLeft: 10 }}>
              <Grid item xs={6}>
                <h6 className="system-data">Make</h6>
                <p className="api-data">
                  {systemInfo.make ? systemInfo.make : "-----"}
                </p>
              </Grid>
              <Grid item xs={6}>
                <h6 className="system-data">Software Version</h6>
                <p className="api-data">
                  {systemInfo.softwareVersion
                    ? systemInfo.softwareVersion
                    : "-----"}
                </p>
              </Grid>
            </Grid>
          </> : <div className="system-loader">
            <CircularProgress />
          </div>}
      </div>
      <div className="dashboard-overview-container">
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <img src={downIcon} className="lan-icon" />
            <h2 className="lan-titles">Overview</h2>
            <h6 className="lan-sub">Check the overall status</h6>
            <hr style={{ marginLeft: 15, marginRight: 17, marginTop: "-18px", color: "#E0E5E5" }} />
          </Grid>
          {routerName ?
            <>
              {overViewData.map((item) => (
                <Grid
                  container
                  spacing={2}
                  style={{ marginLeft: 15, marginTop: 3 }}
                  key={item.overviewName}
                >
                  <Grid item xs={2}>
                    <img src={item.overviewIcon} className="overview-icons" />
                  </Grid>
                  <Grid item xs={6}>
                    <Typography gutterBottom>{item.overviewName}</Typography>
                  </Grid>
                  <Grid item xs={4}>
                    <Typography>
                      {item.overview ? (
                        <span style={{ color: "green" }}>Enabled</span>
                      ) : (
                        <span style={{ color: "Red" }}>Disabled</span>
                      )}
                    </Typography>
                  </Grid>
                </Grid>
              ))}
            </> : <div className="overview-loader">
              <CircularProgress />
            </div>}
        </Grid>
      </div>
    </>
  );
};

export default DashBoard;
